import { Component, OnInit } from '@angular/core';
import { async } from '@angular/core/testing';
import { Router } from '@angular/router';
import { Device } from '@ionic-native/device/ngx';
import { AlertController } from '@ionic/angular';
import { EDGuardarPermisoTrabajoAPP } from 'src/app/interfaces/edguardar-permiso-trabajo-app';
import { EDInfoAuditoriaAPP } from 'src/app/interfaces/edinfo-auditoria-app';
import { TrabajadorMigrado } from 'src/app/interfaces/trabajador-migrado';
import { DataBaseService } from 'src/app/services/SST/data-base.service';
import { DataService } from 'src/app/services/SST/data.service';
import { ContextService } from 'src/app/services/context.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-liberar-trabajadores',
  templateUrl: './liberar-trabajadores.page.html',
  styleUrls: ['./liberar-trabajadores.page.scss'],
})
export class LiberarTrabajadoresPage implements OnInit {
  trabajadores: TrabajadorMigrado[] = [];
  user: any;
  auditoria: EDInfoAuditoriaAPP = new EDInfoAuditoriaAPP();
  currentPage: number = 1;
  workersPerPage: number = 10;
  marcarTodos: any;
  trabajadoresMarcados: any[] = [];
  totalPage: number = 0;
  data: EDGuardarPermisoTrabajoAPP[];

  constructor(
    private dataBaseService: DataBaseService,
    private utils: UtilsService,
    private context: ContextService,
    private device: Device,
    private alertController: AlertController,
    private dataService: DataService,
    private router: Router
  ) {}

  ngOnInit() {
    this.trabajadoresPorLiberar();
    this.dataBaseService.open().then(() => {
      this.dataBaseService.getAllData().subscribe({
        next: (data: EDGuardarPermisoTrabajoAPP[]) => {
          this.data = data;
          this.updateTrabajadores();
        },
        error: error => {
          this.dataBaseService.close();
        },
      });
    });
  }

  async trabajadoresPorLiberar() {
    console.log('trabajadoresPorLiberar');
    try {
      await this.utils.presentLoading();
      this.user = await this.context.get();
      await this.dataBaseService.open().then(() => {
        this.dataBaseService.getAllTrabajadoresMigrados().subscribe({
          next: (trabajadores: TrabajadorMigrado[]) => {
            this.utils.dismissLoading();
            trabajadores.forEach((trabajador: TrabajadorMigrado) => {
              trabajador.accion = false;
            });
            this.trabajadores = trabajadores;
            this.totalPage = Math.ceil(this.trabajadores.length / this.workersPerPage);
          },
          error: error => {
            console.error('Error al obtener los trabajadores disponibles:', error);
            this.dataBaseService.close();
          },
          complete: () => {
            console.log('Trabajadores disponibles completados');
            this.utils.dismissLoading();
          },
        });
      });
    } catch (error) {
      console.error('Error al liberar trabajadores:', error);
      this.dataBaseService.close();
      this.utils.dismissLoading();
    }
  }

  liberarUser() {
    let idsUser: number[] = [];
    let usuariosUtiliados: TrabajadorMigrado[] = [];
    this.trabajadores.forEach((trabajador: TrabajadorMigrado) => {
      if (trabajador.accion) {
        idsUser.push(trabajador.PK_ID_PermisoAlturas_PersonasAMigrar);
      }
      return idsUser;
    });

    this.trabajadores.forEach((trabajador: TrabajadorMigrado) => {
      if (trabajador.blnActivo && trabajador.accion) {
        usuariosUtiliados.push(trabajador);
      }
      return usuariosUtiliados;
    });

    if (usuariosUtiliados.length > 0) {
      let nombres = usuariosUtiliados.map((trabajador: TrabajadorMigrado) => {
        return trabajador.strNombreTrabajador;
      });
      return this.mesaageAlert('No se pueden liberar trabajadores que esten en un permiso pendiente: \n' + nombres);
    }

    if (idsUser.length === 0) return this.mesaageAlert('Debe seleccionar al menos un trabajador');
    let permios;
    /* estas seguro de eliminar el usuario */
    /* si es cordova colocar web */
    this.auditoria.strUUID = this.device.uuid ? this.device.uuid : 'web';
    this.auditoria.strIdentificacionUsuario = this.user['_trabajador']._id;
    this.auditoria.strNombreUsuario = this.user['_trabajador'].nombre;
    this.auditoria.strNombreEmpresa = this.user['_empresa']._razonSocial;
    this.auditoria.strTipoDocEmpresa = this.user['_empresa']._tipoDocumento;
    this.auditoria.strDocumentoEmpresa = this.user['_empresa']._id;
    this.alertController
      .create({
        header: 'Liberar trabajador',
        message: `¬øEstas seguro de liberar a los trabajadores?`,
        buttons: [
          {
            text: 'Cancelar',
            cssClass: 'color: green !important',
            role: 'cancel',
            handler: () => {
              console.log('Cancelado');
            },
          },
          {
            text: 'Aceptar',
            cssClass: 'color: red !important',
            handler: async () => {
              await this.utils.presentLoading();
              await this.dataService.liberarTrabajador(idsUser, this.auditoria);
              await this.trabajadoresPorLiberar();
              this.utils.dismissLoading();
            },
          },
        ],
      })
      .then(alert => {
        alert.present();
      });
  }

  onChangeMarcarTodos(event: any) {
    const isChecked = event.detail.checked;
    this.trabajadores = this.trabajadores.map(trabajador => {
      if (this.isOnCurrentPage(trabajador)) {
        trabajador.accion = isChecked;
      }
      return trabajador;
    });

    if (isChecked) {
      this.trabajadores
        .filter(trabajador => !this.trabajadoresMarcados.includes(trabajador))
        .forEach(trabajador => this.trabajadoresMarcados.push(trabajador));
    } else {
      this.trabajadoresMarcados = this.trabajadoresMarcados.filter(trabajador => !trabajador.accion);
    }
  }

  isOnCurrentPage(trabajador: any): boolean {
    const index = this.trabajadores.indexOf(trabajador);
    const startIndex = (this.currentPage - 1) * this.workersPerPage;
    const endIndex = this.currentPage * this.workersPerPage;
    return index >= startIndex && index < endIndex;
  }

  previousPage() {
    if (this.currentPage > 1) {
      this.currentPage--;
      this.updatePage();
    }
  }

  nextPage() {
    if (this.currentPage * this.workersPerPage < this.trabajadores.length) {
      this.currentPage++;
      this.updatePage();
    }
  }

  updatePage() {
    const trabajadoresEnPaginaActual = this.trabajadores.slice(
      (this.currentPage - 1) * this.workersPerPage,
      this.currentPage * this.workersPerPage
    );

    const todosMarcados = trabajadoresEnPaginaActual.every(trabajador => trabajador.accion);
    const otrosMarcados = this.trabajadoresMarcados.every(trabajador => trabajador.accion);

    this.marcarTodos = todosMarcados && otrosMarcados;
  }

  private mesaageAlert(message: string) {
    this.alertController
      .create({
        header: 'Informaci√≥n',
        message,
        buttons: ['Aceptar'],
      })
      .then(alert => {
        alert.present();
      });
  }
  pushPermisosSST(): void {
    this.router.navigate(['/home/permisos']);
  }

  async updateTrabajadores() {
    try {
      this.data.forEach(permisoIncompleto => {
        permisoIncompleto['listaTrabajadoresMigrados'].forEach(async trabajador => {
          console.log(trabajador);
          await this.dataBaseService.updateTrabajadorMigrado(trabajador);
        });
      });
    } catch (error) {}
  }

  volver() {
    this.router.navigate(['/home/permisos']);
  }
  back() {
    this.router.navigate(['/home/permisos']);
  }
}
